      </v-col>
    </v-row>
    <v-row v-if="Object.keys(outputEventList).length > 0">
      <v-col cols="12">
        <h2></h2>
        <v-carousel
          cycle
          hide-delimiters
          :show-arrows="false"
          style="max-width: 800px; height: auto; margin: 0 auto;"
        >
          <v-carousel-item
            v-for="(event, eventName) in outputEventList"
            :key="eventName"
            class="text-center"
          >
            <v-card variant="flat">
              <a :href="event.url" target="_blank" class="mainVisual">
                <v-img
                  class="white--text align-end text-center"
                  :src="require(`@/assets/event_information/${event.img}`)"
                ></v-img>
              </a>
              <v-card-title>
                {{ event.title }}
                <div v-if="countDown(eventName) > 0">
                  {{ event.text }}<span style="display: inline-block;"><b style="color: red;">{{ countDown(eventName) }}</b></span>
                </div>
                <div v-else-if="countDown(eventName) === 0">
                  {{ event.text }}<span style="display: inline-block;"><b style="color: red;"><span v-if="event.type === 'live'"></span><span v-else></span></b></span>
                </div>
              </v-card-title>
            </v-card>
          </v-carousel-item>
        </v-carousel>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <h2></h2>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <v-btn block @click="pageMove('simulation')">Simulation</v-btn>
      </v-col>
      <v-col>
        <v-btn block @click="pageMove('cardlist')">Card List</v-btn>
      </v-col>
      <v-col>
        <v-btn block @click="pageMove('musiclist')">Music List</v-btn>
      </v-col>
      <v-col>
        <v-btn block @click="pageMove('itemlist')">Item List</v-btn>
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <h2>About</h2>
        LinkLike()()<br>
        <br>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12">
        <h2>Attention</h2>
        PC<br>
        
      </v-col>
      <v-col cols="12">
        ()<br>
        SNS
      </v-col>
      <v-col cols="12">
        <br>
        
      </v-col>
      <v-col cols="12">
        <br>
        <br>
        ()
      </v-col>
      <v-col cols="12">
        <br>
        SNS
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12">
        <h2>Page Introduction</h2>
        
      </v-col>
      <v-col cols="12">
        <b>SIMULATION(Pt.)</b><br>
        Pt.<br>
        
      </v-col>
      <v-col cols="12">
        <b>CARD LIST()</b><br>
        ///
      </v-col>
      <v-col cols="12">
        <b>ITEM LIST()</b><br>
        Quest Live
      </v-col>
      <v-col cols="12">
        
      </v-col>
    </v-row>
    <v-row>
      <v-col>
        <h2>Update Information</h2>
        ()<br>
        <a href="https://github.com/taira-no-atsumori/llllMgr/releases" target="_blank">Update Information</a>
      </v-col>
    </v-row>
  </v-container>
</template>

<script setup>
  import { useStoreCounter } from '../stores/counter';
  const store = useStoreCounter();
</script>

<script>
export default {
  name: 'Home',
  components: {},
  data() {
    return {
      eventList: {
        'liveGP': {
          title: '1038',
          text: '',
          type: 'liveGP',
          firstDay: [2024, 2, 10, 10, 0],
          lastDay: [2024, 2, 16, 3, 59],
          url: 'https://www.lovelive-anime.jp/hasunosora/appnews/detail/?p=2024-02-08-10-r1h1jj6fmf',
          img: '1038_logo.png'
        },
        /*'liveGP': {
          title: ' -Exhibition-',
          text: '',
          type: 'liveGP',
          firstDay: [2024, 1, 24, 10, 0],
          lastDay: [2024, 1, 31, 3, 59],
          url: 'https://www.lovelive-anime.jp/hasunosora/appnews/detail/?p=2024-01-22-30-bn39wqn69c',
          img: ' Exhibition_logo.png'
        },*/
        'unitKoushien': {
          title: 'LoveLive! Series Presents  2024',
          firstDay: [2024, 3, 9, 0, 0],
          lastDay: [2024, 3, 10, 0, 0],
          url: 'https://lovelive-anime.jp/special/live/live_detail.php?p=unitlive2024',
          text: '',
          type: 'live',
          img: 'unitKoushien_logo.png'
        },
        '2ndLive_chiba': {
          title: '  2nd Live Tour Blooming with ',
          text: '',
          type: 'live',
          firstDay: [2024, 4, 20, 0, 0],
          lastDay: [2024, 4, 21, 0, 0],
          url: 'https://www.lovelive-anime.jp/hasunosora/live-event/live_detail.php?p=BLW',
          img: '2ndLIVE_logo.png'
        },
        '2ndLive_hyougo': {
          title: '  2nd Live Tour Blooming with ',
          text: '',
          type: 'live',
          firstDay: [2024, 5, 18, 0, 0],
          lastDay: [2024, 5, 19, 0, 0],
          url: 'https://www.lovelive-anime.jp/hasunosora/live-event/live_detail.php?p=BLW',
          img: '2ndLIVE_logo.png'
        }
      }
    }
  },
  computed: {
    outputEventList() {
      const result = {};

      for (const key in this.eventList) {
        if (this.countDown(key) >= 0) {
          result[key] = this.eventList[key];
        }
      }

      return result;
    }
  },
  methods: {
    pageMove(movePageName) {
      this.$router.replace(movePageName);
    },
    countDown(eventName) {
      const firstDay = new Date(this.eventList[eventName].firstDay[0], (this.eventList[eventName].firstDay[1] - 1), this.eventList[eventName].firstDay[2], this.eventList[eventName].firstDay[3], this.eventList[eventName].firstDay[4], 0);
      let countDay = this.dateCul(firstDay);

      if (countDay > 0) {
        return countDay;
      }

      const lastDay = new Date(this.eventList[eventName].lastDay[0], (this.eventList[eventName].lastDay[1] - 1), this.eventList[eventName].lastDay[2], this.eventList[eventName].lastDay[3], this.eventList[eventName].lastDay[4], 0);

      if (this.dateCul(lastDay) >= 0) {
        return 0;
      } else {
        return -1;
      }
    },
    dateCul(targetDay) {
      const today = new Date();
      return -(Math.floor((today.getTime() - targetDay.getTime()) / (1000 * 60 * 60 * 24)));
    }
  }
}
</script>

<style lang="scss" scoped>
.mainVisual{
  &:hover {
    opacity: 0.75;
  }
}

@media screen and (max-width: 600px) {}
</style>